<script type="text/javascript">
  export default {
    name: 'SoluTion',
    data() {
      return {
        SoluIndexQ:null,
        SoluIndexA:null
      }
    },
    computed: {

    },
    methods: {
      moveLeft (e) {
          let lis = $(".api .js-list li");
          var index = 2;
          if(this["SoluIndexQ"]==null){
            index = 2;
          }else {
            index = this["SoluIndexQ"];
          }
          if(index==4){
            index = -1;
          }
          this.slide(++index);
          this["SoluIndexQ"] = index;
      },
      moveRight (e) {
        let lis = $(".api .js-list li");
        var index = 2;
        if(this["SoluIndexA"]==null){
          index = 2;
        }else {
          index = this["SoluIndexA"];
        }
        if(index==-1){
          index = 4;
        }
        this.slide(--index);
        this["SoluIndexA"] = index;
      },

      slide(n_index) {
        var index = n_index,max = 4,
          lis = $(".api .js-list li"),
          iArr = [4, 5, 1, 2, 3, 4, 5, 1, 2];
          index > max ? index = 0 : index < 0 && (index = max), lis.each(function(i) {
          var num = iArr[i - index + 4];
          $(this).removeClass().addClass("index-" + num)
            console.log(n_index)
        })
      }
    },
    mounted () {
      let lis = $(".api .js-list li");
      var content = this;
      lis.each(function(i) {
        $(this).on("click", function() {
          console.log($(this).index());
          console.log(this.getAttribute("class"))
          if($(this).index()==0){
            content.slide(0);
          }else if($(this).index()==1) {
            content.slide(1);
          }else if($(this).index()==2) {
            content.slide(2);
          }else if($(this).index()==3) {
            content.slide(3);
          }else if($(this).index()==4) {
            content.slide(4);
          }
        })
      })
    }
  }
