import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index/Index'
import SoluTion from '@/components/solution/Index'
import CubeNews from '@/components/cubenews/Index'
import AboutUs from '@/components/aboutus/Index'
Vue.use(Router)

let routs= new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Index
    },
    /*首页*/
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    /*解决方案*/
    {
      path: '/solution',
      name: 'solution',
      component: SoluTion
    },
    /*新闻动态*/
    {
      path: '/cubenews',
      name: 'cubenews',
      component: CubeNews
    },
    /*关于我们*/
    {
      path: '/aboutus',
      name: 'aboutus',
      component: AboutUs
    },
    {
      path: '*',
      component: Index
    }
  ]
});

routs.afterEach((to, from, next) => {
  var linkTo = to.name;
  setTimeout(function () {
    switch (linkTo){
      case "index":
        $("#index").addClass("active");
        $("#solution").removeClass("active");
        $("#cubenews").removeClass("active");
        $("#aboutus").removeClass("active");
        break;
      case "solution":
        $("#solution").addClass("active");
        $("#index").removeClass("active");
        $("#cubenews").removeClass("active");
        $("#aboutus").removeClass("active");
        break;
      case "cubenews":
        $("#cubenews").addClass("active");
        $("#solution").removeClass("active");
        $("#index").removeClass("active");
        $("#aboutus").removeClass("active");
        break;
      case "aboutus":
        $("#aboutus").addClass("active");
        $("#solution").removeClass("active");
        $("#cubenews").removeClass("active");
        $("#index").removeClass("active");
        break;
      default:
        $("#index").addClass("active");
        $("#solution").removeClass("active");
        $("#cubenews").removeClass("active");
        $("#aboutus").removeClass("active");
        break;
    }
  },10)

  //next();
})

/*routs.beforeRouteLeave((to,from)=>{
  debugger
  console.log('进入leave路由钩子')

})
routs.beforeRouteUpdate((to,from, next)=>{
  debugger
  console.log('进入leave路由钩子')

})*/



export default routs;



