<template>
  <div class="header">
    <div class="head">
      <div class="container">
        <div class="navbar-top">
          <div class="navbar-header">
            <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1">
              <span class="sr-only">Toggle navigation</span>
              <span class="icon-bar"></span>
              <span class="icon-bar"></span>
              <span class="icon-bar"></span>
            </button>
            <div class="navbar-brand logo">
                <h1  class="animated wow pulse" data-wow-delay=".5s">前海汇金融服务有限公司</h1>
            </div>
          </div>
          <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
              <ul class="nav navbar-nav link-effect-4">
                <li class="active" id="index"><a href="/index" data-hover="首页">首页</a> </li>
                <li id="solution"><a href="/solution" data-hover="解决方案"> 解决方案</a></li>
                <li id="cubenews"><a href="/cubenews"  data-hover="新闻动态">新闻动态</a></li>
                <li  id="aboutus"><a href="/aboutus" data-hover="关于我们">关于我们 </a> </li>
              </ul>
          </div>
        </div>
        <div class="clearfix"></div>
      </div>
    </div>
    <div ref='topBanners' class="top-banners">
      <div class="banners">
        <div class="banner0 current">
          <div class="content">
            <!--依托科技力量  打造智慧金融-->
          </div>
        </div>
        <div class="banner1">
          <div class="content">
            <!--依托科技力量  打造智慧金融-->
          </div>
        </div>
        <div class="banner2">
          <div class="content">
            <!--依托科技力量  打造智慧金融-->
          </div>
        </div>
      </div>
      <div class="banner-toggle">
        <div @mouseover="bannerBtnOver" @mouseout="bannerBtnOut" class="toggle-btn toggle-current">
        </div>
        <div @mouseover="bannerBtnOver" @mouseout="bannerBtnOut" class="toggle-btn">
        </div>
        <div @mouseover="bannerBtnOver" @mouseout="bannerBtnOut" class="toggle-btn">
        </div>
      </div>
    </div>

  </div>
</template>

<script type="text/javascript">
  export default{
    name: 'TopHead',
    data(){
      return {
        bannerIndex:0,
        isToggleBanner:true
      }
    },
    mounted:function(){
       this.init();
       //this.activeChoose();
    },
    methods:{
      init: function(res) {
          let _this=this;
          setInterval(() => {
            _this.toggleBanner();
          }, 5000);
      },
      toggleBanner:function(){
        if(!this.isToggleBanner)
        {
          return;
        }
        this.bannerIndex++;
        let nodes=this.$refs.topBanners.querySelector(".banners").children;
        let togglebtns=this.$refs.topBanners.querySelector(".banner-toggle").children;
        if(nodes.length<=this.bannerIndex)
        {
          this.bannerIndex=0;
        }
        for (let i = 0; i < nodes.length; i++) {
            if(this.bannerIndex==i)
            {
                nodes[i].className=nodes[i].className+" current";
            }
            else
            {
              nodes[i].className=nodes[i].className.replace(/( current)+/g,"");
            }
        }
        for (let i = 0; i < togglebtns.length; i++) {
            if(this.bannerIndex==i)
            {
                togglebtns[i].className=togglebtns[i].className+" toggle-current";
            }
            else
            {
                togglebtns[i].className=togglebtns[i].className.replace(/( toggle-current)+/g,"");
            }
        }
      },
      bannerBtnOver:function(e)
      {
        let togglebtns=this.$refs.topBanners.querySelector(".banner-toggle").children;
        for (let i = 0; i < togglebtns.length; i++) {
          if(e.target==togglebtns[i])
          {
            this.bannerIndex=i-1;
          }
        }
        this.toggleBanner();
        this.isToggleBanner=false;
      },
      bannerBtnOut:function(e)
      {
        this.isToggleBanner=true;
      },
      activeChoose:function () {
        let cg = $("ul li");
        var content = this;
        cg.each(function(i) {
          $(this).on("click", function() {
            $(this).addClass("active");
            $(this).siblings().each(function(k) {
              $(this).removeClass("active");
            })
          })
        })
      }

    }
  }
</script>



