<template>
  <div ref="body" class="body">
    <!--我们能为客户创造什么价值-->
    <core-tech></core-tech>
    <!--ITO服务-->
    <server-content></server-content>
    <!--我们能为客户提供什么样的解决方案-->
    <solu-tion></solu-tion>
    <!--公司历程-->
    <company-history></company-history>

  </div>
</template>

<script>
  import SoluTion from '@/components/index/SoluTion'
  import CoreTech from '@/components/index/CoreTech'
  import ServerContent from '@/components/index/ServerContent'
  import CompanyHistory from '@/components/index/CompanyHistory'
  export default {
    name: 'index',
    data () {
      return {

      }
    },
    components: {
      'solu-tion': SoluTion,
      'core-tech': CoreTech,
      'server-content':ServerContent,
      'company-history':CompanyHistory
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



